.capitulos{
    text-decoration: none;
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 5vw;
}
.main-novel a{
    margin:0px;
    border: 0.5vh solid black;
    color: black;
    text-decoration: none;   
    transition-duration: 1000ms;
}
.main-novel a:hover{
    color: var(--triadic);    
    border: 2vh solid var(--triadic);
}