.feed-container{
    background-color: var(--complementary);
    display:flex;
    flex-direction: column;
    width: auto;
    margin-top: 1rem;
    margin-bottom:1rem;
    padding: 1vmax;
    padding-bottom: 3vmax;
    padding-top: 3vmax;  
    border-radius: 10px;
    align-items: center;
    justify-content: center;
}
@media screen and (max-width:400px){
    .feed-container{
        margin-left: 20px;
        margin-right: 20px;
        padding: 20px;
    }

}